document.addEventListener('DOMContentLoaded', () => {

    let slideIndex = 1,
        left = 0,
        dotIndex = 0;        

    const slides = document.querySelectorAll('.slider-promo__item'),
        prev = document.querySelector('.promo .arrows__item--prev'),
        next = document.querySelector('.promo .arrows__item--next'),
        dotsWrap = document.querySelector('.promo .dots'),
        dots = document.querySelectorAll('.promo .dots__dot'),

        slidesReviews = document.querySelectorAll('.slider-reviews__item'),
        prevReviews = document.querySelector('.reviews .arrows__item--prev'),
        nextReviews = document.querySelector('.reviews .arrows__item--next'),
        dotsWrapReviews = document.querySelector('.reviews .dots'),
        dotsReviews = document.querySelectorAll('.reviews .dots__dot'),

        sliderWrapper = document.querySelector('.slider-clients__list'),
        slidesClients = document.querySelectorAll('.slider-clients__image'),
        prevClients = document.querySelector('.clients .arrows__item--prev'),
        nextClients = document.querySelector('.clients .arrows__item--next'),
        dotsClients = document.querySelectorAll('.clients .dots__dot'),
        dotsWrapClents = document.querySelector('.clients .dots'),

        burger = document.querySelector('.burger'),
        menu = document.querySelector('.nav');

    const showSlides = (number) => {

        if (number > slides.length || number > slidesReviews.length) {
            slideIndex = 4;
        }

        if (number < 1) {
            slideIndex = 1;
        }

        if (slideIndex == 1) {
            prev.style.opacity = 0.1;
            prev.style.cursor = 'default';
            prevReviews.style.opacity = 0.1;
            prevReviews.style.cursor = 'default';
        } else {
            prev.style.opacity = 0.9;
            prev.style.cursor = 'pointer';
            prevReviews.style.opacity = 0.9;
            prevReviews.style.cursor = 'pointer';
        }

        if (slideIndex == 4) {
            next.style.opacity = 0.1;
            next.style.cursor = 'default';
            nextReviews.style.opacity = 0.1;
            nextReviews.style.cursor = 'default';
        } else {
            next.style.opacity = 0.9;
            next.style.cursor = 'pointer';
            nextReviews.style.opacity = 0.9;
            nextReviews.style.cursor = 'pointer';
        }

        for (let i = 0; i < slides.length; i++) {
            slides[i].style.display = 'none';
        }

        for (let i = 0; i < slidesReviews.length; i++) {
            slidesReviews[i].style.display = 'none';
        }

        for (let i = 0; i < dots.length; i++) {
            dots[i].classList.remove('dots__dot--active');
        }

        for (let i = 0; i < dotsReviews.length; i++) {
            dotsReviews[i].classList.remove('dots__dot--active');
        }

        slides[slideIndex - 1].style.display = 'block';
        slidesReviews[slideIndex - 1].style.display = 'block';
        dots[slideIndex - 1].classList.add('dots__dot--active');
        dotsReviews[slideIndex - 1].classList.add('dots__dot--active');
    };

    const plusSlides = (number) => {
        showSlides(slideIndex += number);
    };

    const currentSlide = (number) => {
        showSlides(slideIndex = number);
    };

    prev.addEventListener('click', () => {
        plusSlides(-1);
    });

    prevReviews.addEventListener('click', () => {
        plusSlides(-1);
    });

    next.addEventListener('click', () => {
        plusSlides(1);
    });

    nextReviews.addEventListener('click', () => {
        plusSlides(1);
    });

    dotsWrap.addEventListener('click', (event)=> {
        for (let i = 0; i < dots.length + 1; i++) {
            if (event.target.classList.contains('dots__dot') && event.target == dots[i-1]) {
                currentSlide(i);
            }
        }
    });

    dotsWrapReviews.addEventListener('click', (event)=> {
        for (let i = 0; i < dotsReviews.length + 1; i++) {
            if (event.target.classList.contains('dots__dot') && event.target == dotsReviews[i-1]) {
                currentSlide(i);
            }
        }
    });

    dotsWrapClents.addEventListener('click', (event) => {
        for (let i = 0; i < dotsClients.length; i++) {
            dotsClients[i].classList.remove('dots__dot--active');
            
            if (event.target.classList.contains('dots__dot') && event.target == dotsClients[i]) {
                dotsClients[i].classList.add('dots__dot--active');
            }
        }

        if(event.target.dataset.dot == 'one') {
            sliderWrapper.style.left = 0;
            prevClients.style.opacity = 0.1;
            prevClients.style.cursor = 'default';
            nextClients.style.opacity = 0.9;
            nextClients.style.cursor = 'pointer';
        } else if (event.target.dataset.dot == 'two') {
            sliderWrapper.style.left = -280 + 'px';
            prevClients.style.opacity = 0.9;
            prevClients.style.cursor = 'pointer';
            nextClients.style.opacity = 0.9;
            nextClients.style.cursor = 'pointer';  
        } else if (event.target.dataset.dot == 'three') {
            sliderWrapper.style.left = -560 + 'px';
            prevClients.style.opacity = 0.9;
            prevClients.style.cursor = 'pointer';
            nextClients.style.opacity = 0.9;
            nextClients.style.cursor = 'pointer';  
        } else if (event.target.dataset.dot == 'four') {
            sliderWrapper.style.left = -840 + 'px';
            prevClients.style.opacity = 0.9;
            prevClients.style.cursor = 'pointer'; 
            nextClients.style.opacity = 0.1;
            nextClients.style.cursor = 'default'; 
        }
    });

    prevClients.style.cssText = `opacity: 0.1; cursor: default;`;

    prevClients.addEventListener('click', () => {
        for (let i = 0; i < dotsClients.length; i++) {
            dotsClients[i].classList.remove('dots__dot--active');
        }
        nextClients.style.opacity = 0.9;
        nextClients.style.cursor = 'pointer';
        dotIndex--;

        if (dotIndex <= 0) {
            dotIndex = 0;
            dotsClients[dotIndex].classList.add('dots__dot--active');
            prevClients.style.opacity = 0.1;
            prevClients.style.cursor = 'default';
        } else {
            dotsClients[dotIndex].classList.add('dots__dot--active');
        }

        left -= -280;
        if (left > 0) {
            left = 0;
        }
        sliderWrapper.style.left = left + 'px';
    });

    nextClients.addEventListener('click', () => {

        for (let i = 0; i < dotsClients.length; i++) {
            dotsClients[i].classList.remove('dots__dot--active');
        }

        prevClients.style.opacity = 0.9;
        prevClients.style.cursor = 'pointer';        
        dotIndex++;

        if (dotIndex >= 3) {
            dotIndex = 3;
            dotsClients[dotIndex].classList.add('dots__dot--active');
            nextClients.style.opacity = 0.1;
            nextClients.style.cursor = 'default';
        } else {
            dotsClients[dotIndex].classList.add('dots__dot--active');
        }       
        
        left -= 280;
        if (left < -840) {
            left = -840;
        }        
        sliderWrapper.style.left = left + 'px'; 
    });

    const toggleClass = () => {
        burger.classList.toggle('burger--active');
        menu.classList.toggle('nav--active');
    
        if(burger.classList.contains('burger--active')) {
          document.body.style.overflow = 'hidden';
        } else {
          document.body.style.overflow = '';
        }
    }

    burger.addEventListener('click', e => {
        e.preventDefault();
        toggleClass();
    });

    showSlides(slideIndex);

    

});